<template>
  <div class="user-detail menu-manage">
    <base-panel class="menu-list-wrap" :no-title="noTitle">
      <div class="left-item-header">
        <span class="item-header-icon">菜单列表</span>
        <el-button
          @click="handleInitMenu"
          style="float: right;margin-top: 10px;"
          >初始化</el-button
        >
      </div>
      <div class="role-list">
        <div class="role-list-title">
          <div style="width:100%;display:inline-block;">
            <el-input
              v-model="menuTreeStr"
              style="width:300px"
              placeholder="请输入过滤条件"
              class="showBackground"
              auto-complete="on"
            >
              <div slot="suffix" ref="inputColor1" class="backgroundChange">
                <icon class="icon" name="icon_search" />
              </div>
            </el-input>
          </div>
          <div class="clear"></div>
        </div>
        <div class="role-list-tree">
          <el-tree
            key="menuTree"
            ref="menuTree"
            :default-expand-all="true"
            :data="trees"
            :props="defaultProps"
            node-key="id"
            :current-node-key="menu"
            :render-after-expand="false"
            :default-expanded-keys="defaultProExpands"
            :default-checked-keys="menus"
            :filter-node-method="filterNode"
            @check-change="handleActionsChange"
          >
            <div slot-scope="{ node, data }" class="item-warp">
              <div
                class="info-item"
                @click="
                  stopPropagationEvent(event, data, node.label);
                  authPage(data);
                "
              >
                <div class="label">
                  <span v-if="!isEdit[data.$treeNodeId]" class="menu-title"
                    >{{ node.label }}
                  </span>
                  <el-input
                    v-if="isEdit[data.$treeNodeId]"
                    v-model="editTitleStr[data.$treeNodeId]"
                    style="width: 170px"
                    class="showBackground"
                    auto-complete="on"
                    :placeholder="node.label"
                    minlength="1"
                  >
                  </el-input>
                  <div
                    v-if="!isEdit[data.$treeNodeId]"
                    class="icon-wrap"
                    @click="
                      () => {
                        return false;
                      }
                    "
                  >
                    <el-tooltip
                      class="item "
                      effect="dark"
                      content="编辑"
                      :enterable="false"
                      placement="top"
                    >
                      <span
                        class="menu-item-icon"
                        @click="
                          () => {
                            showMenus(data, node, event);
                          }
                        "
                      >
                        <svg-icon
                          icon-class="icon_edit"
                          name="icon_edit"
                          class="icon icon_edit"
                        ></svg-icon>
                      </span>
                    </el-tooltip>
                  </div>
                  <div
                    v-if="isEdit[data.$treeNodeId]"
                    class="icon-wrap"
                    style="display: inline-block"
                    @click="
                      () => {
                        return false;
                      }
                    "
                  >
                    <el-tooltip
                      class="item "
                      effect="dark"
                      content="确认编辑"
                      :enterable="false"
                      placement="top"
                    >
                      <span
                        class="menu-item-icon"
                        style="margin-right: 10px;"
                        @click="
                          () => {
                            saveEdit(data);
                          }
                        "
                      >
                        <svg-icon
                          icon-class="icon_correct"
                          name="icon_correct"
                          class="icon icon_correct"
                        ></svg-icon>
                      </span>
                    </el-tooltip>
                    <el-tooltip
                      :enterable="false"
                      class="item"
                      effect="dark"
                      content="取消编辑"
                      placement="bottom"
                    >
                      <span
                        class="menu-item-icon"
                        @click="
                          () => {
                            isEdit = [];
                            editTitleStr = [];
                          }
                        "
                      >
                        <svg-icon
                          icon-class="icon_cross"
                          name="icon_cross"
                          class="icon icon_cross"
                          style="width: 12px;height: 12px;"
                        ></svg-icon>
                      </span>
                    </el-tooltip>
                  </div>
                </div>
              </div>
            </div>
          </el-tree>
        </div>
      </div>
    </base-panel>
    <div class="menu-content-wrap">
      <addPage
        :add-boolean="addBoolean"
        :auth-page="authPage"
        :actions-menu-list="actionsMenuList"
        :menu="menu"
        :actions="actions"
        :action-list="actionList"
        :label="label"
        :is-show-btn="isShowBtn"
        :is-show-private-region="isShowPrivateRegion"
        :activeName.sync="activeName"
        :memberList.sync="memberList"
        :userTotal.sync="userTotal"
        :page.sync="page"
        :limit.sync="limit"
      >
      </addPage>
    </div>
  </div>
</template>

<script>
import addPage from './addPage';
import {
  editmenuwq,
  queryMenus,
  initMenu,
  editoOrder,
  menuList,
  munuEditV1
} from '../../../api/authority';
import { arrIntersection } from '@utils/commonUtil';
import routesMapConfig from '@router/routes';
import paramsCache from '@mixins/paramCache';
const paramsCacheMixin = paramsCache([
  'trees',
  'menu',
  'activeName',
  'memberList',
  'page',
  'limit',
  'userTotal',
  'menuTreeStr'
]);
function switchRouterToMenu(parent, routers) {
  const menus = [];
  routers.forEach((item) => {
    const meta = item.meta;
    if (!meta || !meta.authority) {
      return;
    }

    if (meta.hidden) {
      return;
    }

    if (
      item.path.indexOf('home') > -1 ||
      item.path.indexOf('dynamicPage') > -1
    ) {
      return;
    }

    const menu = {};
    menu.code = meta.authority;
    menu.title = meta.title;
    menu.icon = meta.icon;
    if (meta.href) {
      menu.href = meta.href;
    }

    if (item.children) {
      const mc = switchRouterToMenu(menu.code, item.children);
      if (mc.length > 0) {
        menu.children = mc;
      }
    } else {
      const preStr = parent !== '' ? `${parent}_` : '';
      if (meta.actions) {
        menu.actions = meta.actions.map((act) => {
          return {
            describe: act.name,
            regionVoList: [
              {
                code: `${preStr}${menu.code}:${act.code}`,
                name: act.name
              }
            ]
          };
        });
      } else {
        if (parent) {
          menu.actions = [
            {
              describe: '查看',
              regionVoList: [
                {
                  code: `${parent}_${menu.code}:query`,
                  name: '查看'
                }
              ]
            }
          ];
        }
      }
    }

    menus.push(menu);
  });
  return menus;
}

export default {
  name: 'MenuDetail',
  mixins: [paramsCacheMixin],
  components: {
    addPage
  },
  data() {
    return {
      isEdit: [],
      editTitleStr: '',
      label: '未获取到菜单管理列表',
      showData: {},
      showHtml: false,
      addBoolean: false,
      noTitle: true,
      visible: false,
      isShowPrivateRegion: false,
      menuTreeStr: '',
      editTitle: '编辑菜单',
      trees: [],
      defaultProps: {
        children: 'children',
        label: 'title'
      },
      editContent: {
        id: '',
        title: '',
        url: ''
      },
      menus: [],
      actionFlag: {},
      defaultProExpands: [],
      opering: false,
      menu: [],
      actionsMenuList: [],
      actionList: [],
      isShowBtn: false,
      isOne: true,

      activeName: 'userMember2',
      memberList: [],
      userTotal: 0,
      page: 1,
      limit: 10
    };
  },
  created() {
    if (this.trees.length) {
      if (this.menu) {
        !this.memberList && this.authPage(this.menu);
        this.label = this.menu.title;
      } else {
        let defaultMenu = this.trees[0].children[0];
        if (defaultMenu.children && defaultMenu.children.length > 0) {
          defaultMenu = defaultMenu.children[0];
        }
        !this.memberList && this.authPage(defaultMenu);
        this.label = defaultMenu.title;
      }
    } else {
      this.menuquery();
    }
    this.menuTreeStr && this.$refs.menuTree.filter(this.menuTreeStr);
  },
  beforeRouteLeave(to, from, next) {
    this.cacheParamData();
    next();
  },
  watch: {
    menuTreeStr(val) {
      this.$refs.menuTree.filter(val);
    }
  },
  methods: {
    stopPropagationEvent(e, data, label) {
      e = e || window.event;
      e.stopPropagation();
      this.isShowBtn = true;
      this.label = label;
      if (typeof data.children === 'object' && data.children.length >= 0) {
        this.addBoolean = true;
      } else {
        this.addBoolean = false;
      }
      let html = e.target;
      if (this.showHtml) {
        this.showHtml.className = 'el-tree-node__content';
      }
      for (let i = 0; i < 10; i++) {
        html = html.parentNode;
        if (html.className === 'el-tree-node__content') {
          html.className = 'el-tree-node__content isShowAca';
          this.showHtml = html;
          i = 10;
        }
      }
    },
    handleActionsChange(data, checked, indeterminate) {
      if (checked) {
        if (!data.children) {
          this.menus.push(data.id);

          //  设置该菜单下所有功能点为全选
          if (!this.opering) {
            // 防止因功能点选中而循环引发事件
            const as = [];
            if (data.actions) {
              data.actions.forEach((action) => as.push(action.id));
              this.actionFlag[`menu_${data.id}`] = as;
            }
          } else {
            this.opering = false;
          }
        }
      } else {
        const index = this.menus.indexOf(data.id);
        if (index > -1) {
          this.menus.splice(index, 1);

          //  取消选中该菜单下所有功能点
          if (!this.opering) {
            this.actionFlag[`menu_${data.id}`] = [];
          } else {
            this.opering = false;
          }
        }
      }
    },
    filterNode(value, node) {
      if (!value) return true;
      return node.title.indexOf(value) !== -1;
    },
    editTitleTwo(data) {
      this.visible = true;
      this.editTitle = '编辑菜单';
      this.id = data.id;
      this.editContent = {
        id: data.id,
        title: data.title,
        url: data.url
      };
    },
    authPage(data) {
      this.page = 1;
      if (data.children && data.children.length > 0) {
        return;
      }
      if (data.children && !data.actions) {
        this.isShowPrivateRegion = false;
      } else {
        this.isShowPrivateRegion = true;
      }
      // 不携带功能点的菜单直接进入子页面，有功能点的根据选中功能点进入子页面
      if (data && data.actions && data.actions.length > 0) {
        const sfActionIdList = [];
        data.actions.forEach((item) => {
          sfActionIdList.push(item.id);
        });
        const checkedActionIdList = arrIntersection(
          sfActionIdList,
          this.actionList
        );
        this.menu = data;
        this.actions = checkedActionIdList;
        this.actionsMenuList = data.actions;
      } else {
        this.menu = data;
        this.actionsMenuList = [];
      }
    },
    submitSave() {
      if (
        this.editContent.title === '' ||
        this.editContent.title.replace(/^\s+|\s+$/g, '') === ''
      ) {
        this.$notify.error({
          title: '操作失败',
          message: '操作失败'
        });
      } else {
        editmenuwq(this.id, this.editContent.title).then(({ error }) => {
          if (!error) {
            this.$notify.success({
              title: '操作成功',
              message: '操作成功'
            });
            this.menuquery();
          } else {
            this.$notify.error({
              title: '操作失败',
              message: error.message
            });
          }
        });
      }
      this.visible = false;
    },
    collapse(moveNode, inNode, type) {
      if (moveNode.level === 1 && inNode.level === 1) {
        return type === 'prev' || type === 'next';
      }
      if (moveNode.level === 2 && inNode.level === 2) {
        if (moveNode.parent.id === inNode.parent.id) {
          return type === 'prev' || type === 'next';
        }
      }
      if (moveNode.level === 3 && inNode.level === 3) {
        if (moveNode.parent.id === inNode.parent.id) {
          return type === 'prev' || type === 'next';
        }
      }
    },
    nodeDrop(node, lastInner, location, event) {
      editoOrder(this.trees).then(({ error }) => {
        if (!error) {
          this.$notify.success({
            title: '操作成功',
            message: '操作成功'
          });
          queryMenus();
        } else {
          this.$notify.error({
            title: '操作失败',
            message: error.message
          });
        }
      });
    },
    init() {
      console.log(switchRouterToMenu('', routesMapConfig));
      initMenu(switchRouterToMenu('', routesMapConfig)).then(({ error }) => {
        if (!error) {
          this.$notify.success('初始化成功');
          this.menuquery();
        } else {
          this.$notify.error({
            title: '操作失败',
            message: error.message
          });
        }
      });
    },
    handleInitMenu() {
      this.$confirm('您确定要初始化系统中的菜单和功能点吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.init();
        })
        .catch(() => {});
    },
    menuquery() {
      menuList().then(({ data }) => {
        if (data && data.length > 0) {
          this.trees = data;
          if (this.isOne) {
            let defaultMenu = this.trees[0].children[0];
            if (defaultMenu.children && defaultMenu.children.length > 0) {
              defaultMenu = defaultMenu.children[0];
            }
            this.authPage(defaultMenu);
            this.label = defaultMenu.title;
          }
        }
      });
    },
    // 编辑
    showMenus(data, node, event) {
      const e = event || window.event;
      e.stopPropagation();
      this.isEdit = [];
      this.editTitleStr = [];
      this.$set(this.isEdit, [data.$treeNodeId], true);
      this.$set(this.editTitleStr, [data.$treeNodeId], data.title);
      // this.editTitleStr = data.title;
    },
    // 取消编辑状态
    clearShowEdit() {
      this.isEdit = [];
      this.editTitleStr = [];
    },
    // 保存编辑内容
    saveEdit(data) {
      let title;
      if (this.editTitleStr.length) {
        title = this.editTitleStr[data.$treeNodeId];
      }
      if (title === '') {
        this.$message.warning({
          message: '名称不能为空!'
        });
        return false;
      }
      const id = data.id;
      const showLabel = (val, showId) => {
        for (const i in val) {
          if (val[i].id === showId) {
            this.label = val[i].title;
            return;
          } else {
            if (val[i]) {
              if (val[i].children) {
                showLabel(val[i].children);
              }
            }
          }
        }
      };
      // munuEditV1(id, { title })
      munuEditV1(id, title)
        .then((res) => {
          if (res.error) {
            this.$message.error({
              message: `修改菜单列表未能成功!错误信息是${res.error.message};`
            });
          } else {
            this.$message.success({
              message: `修改菜单列表成功!正在重新获取最新菜单列表;`
            });
            this.clearShowEdit();
            this.authPage(this.menu);
            const showId = this.menu.id;
            this.menuquery(showLabel, this.trees, showId);
          }
        })
        .catch((err) => {
          this.$message.error({
            message: `修改菜单列表未能成功!`
          });
        });
    }
  }
};
</script>

<style lang="scss">
@import '~@assets/scss/theme.scss';
.menu-manage {
  .el-tree-node__content {
    height: 40px !important;
    .el-tree-node__expand-icon {
      font-size: 14px;
    }
    .icon-wrap {
      float: right;
      display: none;
      .menu-item-icon {
        color: $link-text-color;
        padding-right: 10px;
      }
    }
    &:hover {
      background-color: $color-main-opcity;
      color: $color-main;
      .icon-wrap {
        display: block;
      }
    }
  }
  .el-tree-node:focus > .el-tree-node__content {
    background-color: $color-main-opcity;
    color: $color-main;
  }
}

.user-detail {
  display: flex;
  border-radius: 4px;
  background-color: $color-back-card;
  transition: background-color 0.8s;
  min-height: calc(100vh - 200px);
  padding: 20px 0 20px 20px;
  font-size: 0;
  .menu-list-wrap {
    height: calc(100vh - 130px - 44px);
    vertical-align: top;
    width: 320px;
    flex-grow: 0;
    display: inline-block;
    border-right: 1px solid $table-border-color;
    border-radius: 0;
    padding: 0 20px 0 0;
    font-size: 14px;
    .left-item-header {
      margin-top: -20px;
      margin-bottom: 20px;
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid $table-border-color;
      &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: -2px;
        width: 5px;
        height: 16px;
        background-color: #2a76cd;
      }
      .item-header-icon {
        position: relative;
        padding-left: 14px;
        color: $color-subtitle-text;
        font-size: 16px;
      }
      .icon_init {
        cursor: pointer;
        width: 14px;
        height: 14px;
        color: $button-selected-color;
      }
    }

    .base-info {
      position: relative;
      padding: 10px;
      margin-bottom: 18px;

      .title {
        margin-bottom: 12px;

        .font3 {
          color: $text-color-help;
          font-weight: 500;
          margin-left: 0.8em;
        }
      }
    }

    .role-list {
      font-size: 14px;
      .role-list-title {
        div {
          .showBackground {
            .backgroundChange {
              position: relative;
              width: 36px;
              height: 32px;
              border-radius: 0 4px 4px 0;
              margin-right: -5px;
              .icon {
                position: absolute;
                right: 10px !important;
                top: 9px;
                width: 14px;
                height: 14px;
                color: #cccccc;
              }
            }
          }
        }
      }

      .role-list-tree {
        margin-bottom: 0;
        border: none;
        height: calc(100vh - 130px - 40px - 35px - 20px - 50px);
        margin-top: 20px;
        overflow: auto;
        &::-webkit-scrollbar {
          width: 8px;
        }
        &::-webkit-scrollbar-thumb {
          border-radius: 6px;
          background: #dbdcdc;
        }
        .el-tree-node__content {
          height: inherit;
        }
        .action {
          border: 0.5px solid #dbdcdc;
        }
      }
    }

    .item-warp {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .info-item {
        display: flex;
        align-items: center;
        width: 100%;
        .label {
          display: block;
          line-height: 26px;
          text-align: left;
          padding: 0 5px 0 0;
          box-sizing: border-box;
          width: 100%;
          .menu-title {
            display: inline-block;
            width: 200px;
          }
        }

        .content {
          margin-left: 20px;
          line-height: 32px;
          position: relative;
          max-height: 100px;
          overflow: hidden;
          width: 100%;
          &.expand {
            max-height: none;
          }
          &-item {
            flex: 0 0 auto;
            margin-right: 40px;
            margin-top: 20px;
            border: 1px solid #ccc;
            border-radius: 5px;
            padding: 2px 10px;
          }

          &:before,
          &:after {
            display: table;
            content: '';
          }

          &:after {
            clear: both;
          }

          .box-col {
            width: 140px;
            height: 32px;
            margin-left: 20px;
            .el-checkbox {
              width: 140px;
              height: 32px;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
          }
        }

        &-add {
          cursor: pointer;
          display: inline-block;
          width: 22px;
          margin-left: 10px;
          text-align: center;
          border: 1px solid rgb(55, 149, 211);
          border-radius: 3px;
        }

        &:before,
        &:after {
          display: table;
          content: '';
        }

        &:after {
          clear: both;
        }
      }

      .edit {
        margin: 0 20px;
      }
    }

    .bottomBtn {
      display: inline-block;
      margin-top: 1em;
      margin-bottom: 1em;
      margin-left: 40%;
    }
  }
  .menu-content-wrap {
    padding: 0 20px 0 0;
    vertical-align: top;
    flex-grow: 1;
    display: inline-block;
    font-size: 14px;
    margin-top: -20px;
    height: calc(100vh - 130px - 22px);
    overflow: auto;
    &::-webkit-scrollbar {
      width: 8px;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 6px;
      background: #dbdcdc;
    }
    .panel-title {
      margin-bottom: 20px;
    }
  }
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 8px;
  }
  .panel .panel-body {
    padding: 0;
  }
}
</style>
