<!-- 菜单拥有的用户和岗位 -->
<template>
  <div class="add-page panelPadd">
    <base-panel no-title>
      <div class="panelPadd">
        <div class="table-common table-three ">
          <base-table-panel
            :title="label"
            :height="362"
            class="sortTable noTableBorder "
            :data-source="memberList"
            :loading="loading"
            :columns="activeName === 'userMember2' ? columns() : columns2()"
            :show-summary="false"
            :total="userTotal"
            has-pagination
            :current-page="page"
            :page-size="limit"
            :border-style="true"
            :cell-class-name="roleCellClassName"
            @handleCurrent="handleCurrentChange"
            @handleSize="handleSizeChange"
          >
            <el-tabs
              v-model="activeName"
              class="el-tab-two"
              @tab-click="activeTabChange"
            >
              <el-tab-pane name="userMember2" label="用户">
                <div class="blank-content"></div>
              </el-tab-pane>
              <el-tab-pane name="groupMember2" label="岗位">
                <div class="blank-content"></div>
              </el-tab-pane>
            </el-tabs>
          </base-table-panel>
        </div>
      </div>
    </base-panel>

    <!--新增-->
    <simple-dialog
      :dialog-title="activeName === 'userMember2' ? '新增用户' : '新增岗位：'"
      :dialog-visible.sync="dialogVisible"
      save-text="确 定"
      cancel-text="取 消"
      botton-show="true"
      class="add-page-form"
      dialog-width="450px"
      @saveEmit="addToProject"
      @cancelEmit="dialogVisible = false"
    >
      <el-form
        ref="numberValidateForm"
        :model="params"
        class="demo-form-inline"
        label-width="110px"
      >
        <el-form-item
          prop="members"
          style="margin-bottom: 10px;margin-top: 10px;"
          :rules="[
            { required: true, message: '此字段不能为空', trigger: 'blur' }
          ]"
          :label="activeName === 'userMember2' ? '选择用户：' : '选择岗位：'"
        >
          <div style="width: 100%;">
            <el-select
              v-if="activeName === 'userMember2'"
              v-model="params.members"
              clearable
              default-first-option
              style="width: 95%;"
              multiple
              filterable
              placeholder="请选择"
            >
              <el-option
                v-for="item in selectedUsers"
                :key="item.id"
                :label="item.username ? item.username : item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
            <el-select
              v-else
              v-model="params.members"
              style="width: 95%;"
              clearable
              multiple
              filterable
              default-first-option
              placeholder="请选择"
            >
              <el-option
                v-for="item in selectedUsers"
                :key="item.id"
                :label="item.username ? item.username : item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </div>
        </el-form-item>
      </el-form>
    </simple-dialog>
    <!--删除-->
    <div
      v-if="dialogVisibleDel"
      class="v-modal"
      tabindex="0"
      style="z-index: 2006;"
    ></div>
    <div
      v-if="dialogVisibleDel"
      class="el-dialog__wrapper"
      style="z-index: 2007;"
    >
      <div class="el-dialog el-dialog--center">
        <div class="el-dialog__body">
          <div style="padding-bottom: 30px;">
            <i class="el-icon-warning" style=""></i>
            <p v-if="activeName !== '新增用户'" style="text-align: center;"
              >是否确认删除“{{ rowItem.username }}”?</p
            >
            <p v-else style="text-align: center;"
              >是否确认删除“{{ rowItem.name }}”?</p
            >
          </div>
          <el-button @click="dialogVisibleDel = false">取 消</el-button>
          <el-button type="primary" @click="remove(rowItem)">确 定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  queryMenus,
  userDictQuery,
  addMembers,
  lookMembers,
  stationList,
  delMembers,
  getDelUserMsg,
  postListDic,
  queryStation,
  querUser
} from '../../../api/authority';
import mixin from '../mixin';
export default {
  name: 'AddPage',
  mixins: [mixin],
  props: {
    label: {
      type: String,
      default: '请选择权限菜单'
    },
    addBoolean: {
      type: Array,
      default: false
    },
    actionList: {
      type: Array,
      default: []
    },
    actionsMenuList: {
      type: Array,
      default: []
    },
    menu: {
      type: Object,
      default: {}
    },
    actions: {
      type: Array,
      default: []
    },
    authPage: {
      type: Function
    },
    isShowBtn: {
      type: Boolean,
      default: false
    },
    isShowPrivateRegion: {
      type: Boolean,
      default: false
    },
    activeName: {
      type: String,
      default: false
    },
    userTotal: {
      type: Number,
      default: false
    },
    memberList: {
      type: Array,
      default: false
    },
    page: {
      type: Number,
      default: false
    },
    limit: {
      type: Number,
      default: false
    }
  },
  data() {
    return {
      rowItem: {},
      dialogVisibleDel: false,
      dialogVisible: false,
      params: {
        region: [],
        members: []
      },
      parentTitle: '',
      title: '',
      selectedUsers: [],
      copyForm: {
        fromUser: {
          id: '',
          usercode: ''
        },
        toUser: [],
        authType: ['0']
      },
      copyVisible: false,
      userData: [],
      keyWord: '',
      allActionsInfo: [],
      regionRules: [],
      columns() {
        return [
          {
            // prop: 'id',
            align: 'center',
            label: '序号',
            width: '70',
            type: 'index'
          },
          {
            prop: 'username',
            label: '姓名',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100'
          },
          {
            prop: 'deptName',
            label: '部门',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '160'
          },
          {
            prop: 'email',
            label: '邮箱',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '160'
          }
        ];
      },
      columns2() {
        return [
          {
            // prop: 'id',
            align: 'center',
            label: '序号',
            width: '70',
            type: 'index'
          },
          {
            prop: 'name',
            label: '岗位名',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '160'
          },
          {
            prop: 'description',
            label: '岗位描述',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
          }
        ];
      }
    };
  },
  created() {
    this.actionList = this.actions;
    if (this.menu && this.menu.id) {
      !this.memberList.length && this.activeTabChange();
    }
  },
  watch: {
    menu: function() {
      this.activeTabChange();
    }
  },
  methods: {
    // 删除角色组
    showRoleDel(row) {
      const e = event || window.event;
      e.stopPropagation();
      this.rowItem = row;
      this.dialogVisibleDel = true;
    },
    //  切换tab页
    activeTabChange() {
      // 切换设置 分页的page=1
      // this.page = 1;
      this.$emit('update:page', 1);
      this.$emit('update:activeName', this.activeName);
      this.$nextTick(() => {
        if (this.activeName === 'userMember2') {
          this.queryusers();
        } else {
          this.querygroups();
        }
      });
    },
    checkTab() {
      return this.activeName === 'userMember2' ? 0 : 1;
    },
    handleSizeChange(val) {
      // this.limit = val;
      // this.page = 1;
      this.$emit('update:page', 1);
      this.$emit('update:limit', val);
      this.$nextTick(() => {
        if (this.checkTab() === 0) {
          this.queryusers();
        } else {
          this.querygroups();
        }
      });
    },
    handleCurrentChange(val) {
      // this.page = val;
      this.$emit('update:page', val);
      this.$nextTick(() => {
        if (this.checkTab() === 0) {
          this.queryusers();
        } else {
          this.querygroups();
        }
      });
    },
    doQuery() {
      this.page = 1;
      this.getUsers();
    },
    // 添加用户或岗位提交按钮
    addToProject() {
      this.$refs.numberValidateForm.validate((valid) => {
        if (valid) {
          this.dialogVisible = false;
          const params = {
            identity: this.checkTab(), // 0用户 1岗位
            members: this.params.members,
            regionIds: this.params.region
          };
          addMembers(this.menu.id, params).then(({ error }) => {
            if (!error) {
              this.$message.success({
                title: '操作成功',
                message: '加入成员成功'
              });
              this.userMembers = [];
              // 刷新界面
              this.keySearch();
            } else {
              this.$message.error({
                title: '操作失败',
                message: error.message
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    // 关键字搜索
    keySearch() {
      this.page = 1;
      if (this.checkTab() === 0) {
        this.queryusers();
      } else {
        this.querygroups();
      }
    },
    // 用户字典查询
    init() {
      const objData = {};
      const userId = this.$store.state.user.userInfo.id;
      objData.userId = userId;
      querUser(objData).then(({ data }) => {
        if (data) {
          this.selectedUsers = data.rows;
        }
      });
    },
    // 岗位字典查询
    initGroup() {
      queryStation().then(({ data }) => {
        if (data) {
          this.selectedUsers = data.rows;
        }
      });
    },
    // 用户列表
    queryusers(value) {
      const params = {
        page: this.page,
        limit: this.limit
      };
      if (this.keyWord !== '') {
        params.queryStr = this.keyWord;
      }

      this.loading = true;
      lookMembers(this.menu.id, params).then((data) => {
        this.loading = false;
        if (data.data) {
          // this.memberList = data.data;
          // this.userTotal = data.data.total;
          this.$emit('update:memberList', data.data.rows);
          this.$emit('update:userTotal', data.data.total);
        }
      });
    },
    // 岗位列表
    querygroups() {
      const params = {
        page: this.page,
        limit: this.limit
      };
      if (this.keyWord !== '') {
        params.queryStr = this.keyWord;
      }
      this.loading = true;
      stationList(this.menu.id, params).then((data) => {
        this.loading = false;
        if (data.data) {
          // this.memberList = data.data;
          // this.userTotal = data.data.total;

          this.$emit('update:memberList', data.data.rows);
          this.$emit('update:userTotal', data.data.total);
        }
      });
    },
    // 删除用户或用户组
    remove(item) {
      const params = {
        members: [item.id],
        identity: this.checkTab()
      };
      delMembers(this.menu.id, params).then(({ error }) => {
        if (!error) {
          this.$message.success({
            title: '删除成功;',
            message: '删除成功;'
          });
          this.dialogVisibleDel = false;
          getDelUserMsg(item.id, { menuId: this.menu.id }).then(({ data }) => {
            if (data.groupVos && data.groupVos.length > 0) {
              let msg = '';
              data.groupVos.forEach((item) => {
                msg = msg + item.name + '、';
              });
              msg = msg.substr(0, msg.length - 1);
              this.$notify({
                title: '提示',
                type: 'warning',
                message: `${item.username}在用户组：${msg}中还存在“${this.menu.title}”菜单的权限`,
                duration: 0
              });
            }
            setTimeout(() => {
              if (data.roleVos && data.roleVos.length > 0) {
                let msg = '';
                data.roleVos.forEach((item) => {
                  msg = msg + item.name + '、';
                });
                msg = msg.substr(0, msg.length - 1);
                this.$notify({
                  title: '提示',
                  type: 'warning',
                  message: `${item.username}在角色：${msg}中还存在“${this.menu.title}”菜单的权限`,
                  duration: 0
                });
              }
            }, 100);
          });
          // 删除成功 刷新列表
          if (this.checkTab() === 0) {
            this.queryusers();
          } else {
            this.querygroups();
          }
          this.dialogVisibleDel = false;
        } else {
          this.$message.success({
            title: '删除失败',
            message: error.message
          });
        }
      });
    },
    findTitle(menuList) {
      menuList.map((menu) => {
        if (menu.id === this.menu.id) {
          this.title = this.menu.title;
        } else {
          if (menu.children && menu.children.length > 0) {
            this.parentTitle = menu.title + '> ';
            menu.children.map((item) => {
              if (item.id === this.menu.id) {
                this.title = this.parentTitle + this.menu.title;
              } else {
                if (item.children && item.children.length > 0) {
                  this.parentTitle += item.title + '>';
                  item.children.map((m) => {
                    if (m.id === this.menu.id) {
                      this.title = this.parentTitle + this.menu.title;
                    }
                  });
                  this.parentTitle = menu.title + '> ';
                }
              }
            });
          }
        }
      });
    },
    menuquery() {
      queryMenus().then(({ data }) => {
        if (data && data.length > 0) {
          this.findTitle(data);
        }
      });
    },
    // 新增按钮
    addBtn() {
      this.dialogVisible = true;
      if (this.activeName === 'userMember2') {
        this.init();
      } else if (this.activeName === 'groupMember2') {
        this.initGroup();
      }
    },
    strTooltip(arr, arr2) {
      let str = '';
      arr.forEach((item, index) => {
        arr2.forEach((ite, ind) => {
          if (item === ite.id) {
            if (str !== '') {
              str += '；';
            }
            str += `${ite.name}`;
          }
        });
      });
      return str;
    }
  }
};
</script>
<style lang="scss">
.user-detail {
  .base-panel {
    box-shadow: none !important;
    padding: 0;
  }
  .base-panel-body {
    .panelPadd {
      padding-top: 0 !important;
    }
  }
}
</style>
<style lang="scss" scoped>
@import '~@assets/scss/theme.scss';
.add-page {
  .el-dialog--center {
    width: 300px;
    margin-top: calc(50vh - 151px);
    .el-dialog__body {
      padding: 20px;
      text-align: center;
      .el-icon-warning {
        width: 100%;
        height: 30px;
        font-size: 30px;
        display: inline-block;
        color: #eaa926;
        margin: 0 auto;
        text-align: center;
        padding-bottom: 10px;
      }
    }
  }
  font-size: 14px;
  padding-left: 20px;
  .actionsMenuList-wrap {
    display: flex;
    .actionsMenuList-label {
      font-weight: 700;
      flex-shrink: 0;
      width: 60px;
    }
  }
  &-title {
    margin-bottom: 12px;

    .search {
      float: left;
      margin-bottom: 20px;

      .search-item {
        display: inline-block;
        width: 280px;
      }
    }
    .table-botton {
      float: right;
    }
  }

  .el-col {
    margin-bottom: 10px;
  }

  .list-actions {
    float: right;
    margin: 0px 20px 10px 20px;
  }
  .data-list .el-input {
    width: 100% !important;
  }
  .data-list {
    border: 1px solid #efefef;
    margin-top: 10px;
    min-height: 270px;
  }
  .userType-list {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    overflow: hidden;
    border-bottom: 1px solid #efefef;
    padding-left: 10px;
    padding-right: 10px;
  }
  .userType-list:hover {
    background-color: rgba(212, 233, 245, 0.3);
  }
  .userType-list:last-of-type {
    border-bottom: none;
  }
  .userType-list-left {
    display: flex;
    min-width: 240px;
    cursor: pointer;
    padding: 10px 20px 10px 10px;
  }
  .user-action-list {
    padding: 10px;
    max-height: 100px;
    max-width: 1000px;
    min-width: 850px;
    box-sizing: border-box;
    .box-col {
      width: 100px;
      height: 32px;
      line-height: 32px;
      margin-left: 20px;
      .el-checkbox {
        width: 100px;
        height: 32px;
        line-height: 32px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
  .userType-list-right {
    padding-top: 28px;
  }
  .userType-list-right a {
    font-size: 12px;
    border: 1px solid #ccc;
    padding: 2px 10px;
  }
  .userType-list-img {
    display: inline-block;
    vertical-align: middle;
    width: 60px;
    height: 60px;
    margin-right: 10px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }

    .head-portrait {
      float: left;
      display: block;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: rgba(#30b3b3, 0.6);
      color: $main-background-color;
      text-align: center;
      line-height: 60px;
      margin-right: 8px;
      font-size: 32px;
    }
  }

  .userType-list-info {
    display: inline-block;
  }
  .userType-list-info a span:nth-of-type(2) {
    margin-left: 10px;
    font-size: 12px;
    background: #eee;
    border-radius: 15px;
    padding: 0 10px;
  }
  .userType-list-info a {
    position: relative;
    top: 10px;
    height: 18px;
    white-space: nowrap;
  }
  .userType-list-info p {
    position: relative;
    top: 15px;
    height: 20px;
  }
  .panelPosition {
    .blueLine {
      top: 30px;
    }
    .el-tab-two {
      display: inline-block;
    }
    .el-tabs__header {
      top: 0px;
    }
    .tabPaneStyle {
      padding: 20px 40px 0px 10px;
    }
    .addInfo {
      margin-top: 20px;
      float: right;
      .form-sub-btn {
        cursor: pointer;
        .icon_save {
          width: 14px;
          height: 14px;
          color: $auxiliary-icon-color;
        }
        &:hover {
          .icon_save {
            color: $button-selected-color;
          }
        }
      }
    }
  }

  .add-page-form {
    .actionsMenuList-label-item {
      display: inline-block;
      line-height: 13px;
      width: 85px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
  .el-form-item__error {
    margin-left: 25%;
  }
}
</style>
